.page {
  font-family: "Cinzel", serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 27px;

  &__body {
    
    &--scroll-off {
      overflow-y: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    transition: transform 0.5s;
  }

  &__explore-online {
    @include onTablet {
      margin: 0 auto;
    }
  }
}
